import goodsHeader from "./goodsHeader.vue"
import interfaces from '../../utils/interface.js'
import popupSpec from './base/popupSpec.vue'
import goodsEval from './goodsEval.vue'



import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			title_price: '', //最低价格
			goodsInit: {}, //初始显示
			swiperList: [], //轮播图
			goodsSpec: [], //商品规格
			goodsInfo: { //选中后的商品信息
				num: 1,
				type: '',
				price: '',
				id : '',
				selectdata: '请选择 颜色 规格' //默认规格		
			},
			data: [], //选中商品
			goodsData: [],
			currentSwiper: 0, //轮播图下标
			spaceInfo: {
				showSpace: false
			}, // 是否展示选择规格遮罩
			entTime: '',
			ids: [],
			key: '',
			showCollect: true
		}
	},
	components: {
		goodsHeader,
		popupSpec,
		goodsEval
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		async initData(e) {
			this.request({
				url: interfaces.get_goods_detail, //取商品详情规格 e商品id
				method: "POST",
				data: {
					goods_id: e
				},
				success: ((res) => {
					console.log(res)
					this.title_price = res.goods[0].price;
					this.goodsInit = res.goods[0]; //初始化信息
					res.data.forEach((item) => {
						this.swiperList.push(item.thumbimg) //提取轮播图
					})
					this.goodsSpec = res.data; //模态框规格
				})
			});
			this.request({
				url: interfaces.get_goods_assess, //取商品评价 e商品id
				method: "POST",
				data: {
					goods_id: e
				},
				success: ((res) => {
					if (res.error_code == 0) {
						res.data.forEach((item) => {
						  item.time =  this.timestampToTime(item.time)
						})
						this.goodsData = res.data;
						console.log(this.goodsData)
					}
				})
			});


























		},
		timestampToTime(cjsj) { //转换时间
			let time = cjsj.toString()
			if(time.length == 10){
				time = time * 1000
			}
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-'
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
			var D = date.getDate() + ' '
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds()
			return Y + M + D
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		},
		setSelect(data, show) { //传入子组件的方法
			this.data = data;
			this.goodsInfo.type = data.color_name; //选中规格
			this.goodsInfo.id = data.id; //选中商品id
			this.goodsInfo.price = data.price_one * this.goodsInfo.num; //选中商品价格
			this.goodsInit.price = data.price_one * this.goodsInfo.num; //选中商品价格
			this.goodsInit.market_price = this.goodsInit.market_price * this.goodsInfo.num;
			this.spaceInfo.showSpace = show;
			this.goodsInfo.selectdata = `已选择 : ${data.color_name} ，数量 : ${this.goodsInfo.num}`;
		},
		add() {
			this.goodsInfo.num++;
		},
		minus() {
			if (this.goodsInfo.num <= 1) {
				return;
			}
			this.goodsInfo.num--;
		},
		//显示选择
		showSpec() {
			this.spaceInfo.showSpace = 'ture';
			this.$refs.Series.bridge() //触发子组件方法
		},
		toGame(e) {

			if (!this.hasLogin) {

				uni.showToast({
					icon: "none",
					title: "请先去登录"
				})
				setTimeout(function() {
					uni.navigateTo({
						url: '../public/login'
					})
				}, 1000)
				return;
			} else if (!this.goodsInfo.type) {
				uni.showToast({
					icon: "none",
					title: "请选择规格"
				})
				return;
			}
			else if (this.goodsInfo.num != 1) {
				uni.showToast({
					icon: "none",
					title: "开砍数量限制1件"
				})
				return;
			}

			let price = e.price;
			uni.setStorageSync('goodsPrice', JSON.stringify(price)); //本地存储用户信息
			// this.goodsInfo.init_price = this.goodsInfo.old; //记录初始价格
			let id = this.goodsInfo.id;
			uni.navigateTo({
				url: '../game/index?goodsId=' + id + '&bottomPrice=' + this.title_price 
			})
		},
		collect() { //点击收藏
			if (this.showCollect) { //未收藏
				if (!this.hasLogin) {

					uni.showToast({
						icon: "none",
						title: "请先去登录"
					})
					setTimeout(function() {
						uni.navigateTo({
							url: '../public/login'
						})
					}, 1000)
					return;
				}
				uni.showToast({
					icon: "none",
					title: "收藏成功"
				}, 500)
				this.showCollect = false;
			} else { //已收藏
				uni.showModal({
					title: '提示',
					content: '确定要取消收藏吗？',
					cancelText: '否',
					confirmText: '是',
					success: res => {
						this.showCollect = true;
					}
				})
			}
		},
		joinCart() { //加入到购物车
			if (!this.hasLogin) {

				uni.showToast({
					icon: "none",
					title: "请先去登录"
				})
				setTimeout(function() {
					uni.navigateTo({
						url: '../public/login'
					})
				}, 1000)
				return;
			}
			// 1.规格
			else if (!this.goodsInfo.type) {
				uni.showToast({
					icon: "none",
					title: "请选择规格"
				})
				return;
			}
			// console.log(this.data)
			//发起网络请求
			this.request({
				url: interfaces.add_car, //加入购物车
				method: "POST",
				data: {
					goods_id: this.data.goods_id, //商品id
					child_id: this.data.id, //商品规格id
					num: this.goodsInfo.num, //商品数量
				},
				success: ((res) => {
					if (res.error_code == 0) {
						uni.showToast({
							icon: "none",
							title: "成功加入购物车"
						})
					} else {
						uni.showToast({
							icon: "none",
							title: "请先去登录"
						})
					}
				})
			});




















		},
		toBuy() { //直接购买
			if (!this.hasLogin) {

				uni.showToast({
					icon: "none",
					title: "请先去登录"
				})
				setTimeout(function() {
					uni.navigateTo({
						url: '../public/login'
					})
				}, 1000)
				return;
			} else if (!this.goodsInfo.type) {
				uni.showToast({
					icon: "none",
					title: "请选择规格"
				})
				return;
			}
			this.data.num = this.goodsInfo.num;
			this.data.old_money = this.goodsInit.market_price;
			this.data.game = '商城';

			uni.navigateTo({
				url: "../order/base/createOrder?goodsInfo=" + JSON.stringify(this.data)
			})
		},
	},
	onLoad(option) {
		let e = option.goods_id;
		this.initData(e);















	}
}
